html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

body {
  background-color: #1e202a;
  font-family: "Inter", sans-serif;
  font-size: 1.4rem;
}

.container {
  margin: 0 2rem;
}

.header {
  height: 25rem;
  padding: 0 2rem;
  padding-top: 5rem;
  background-color: #1f212e;
  border-radius: 0 0 1.5rem 1.5rem;
}
.header h1 {
  color: white;
  margin: 0;
  margin-bottom: 1rem;
  font-size: 2.4rem;
}
.header p {
  color: #8b97c6;
  font-weight: 700;
}
.header__title {
  margin-bottom: 2rem;
  padding: 0 0 2rem 0;
  border-bottom: 1px solid #8b97c6;
}
.header__darkMode {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__darkMode--btn {
  position: relative;
  height: 3rem;
  width: 5.5rem;
  border: none;
  border-radius: 5rem;
  background: linear-gradient(to right, #378fe6, #3eda82);
}
.header__darkMode--btn:hover {
  cursor: pointer;
}
.header__darkMode--btn .toogle {
  position: absolute;
  top: 0.4rem;
  left: 0.4rem;
  height: 2.3rem;
  width: 2.3rem;
  background-color: #1f212e;
  border-radius: 50%;
  transition: all 1s ease-in;
}

.cardSocial {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-top: -4rem;
  margin-bottom: 4rem;
}
.cardSocial__cards {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 23rem;
  width: 33.5rem;
  padding: 3rem 0;
  background-color: #252a41;
  border-radius: 0.5rem;
}
.cardSocial__cards:hover {
  cursor: pointer;
  background-color: #333b5b;
}
.cardSocial__cards--bg {
  position: absolute;
  top: 0;
  left: 0;
  height: 0.5rem;
  width: 100%;
  border-radius: 0.5rem 0.5rem 0 0;
}
.cardSocial__cards--user {
  display: flex;
  align-items: center;
  gap: 1rem;
  font-weight: 700;
  color: #8b97c6;
}
.cardSocial__cards--datas {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cardSocial__cards--datas h2 {
  font-size: 5rem;
  color: white;
}
.cardSocial__cards--datas h3 {
  letter-spacing: 0.3rem;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 400;
  color: #8b97c6;
}
.cardSocial__cards--today {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 700;
  color: white;
}

.facebook {
  background-color: #198ff5;
}

.twitter {
  background-color: #1ca0f2;
}

.instagram {
  background: linear-gradient(to right, #fdc468, #df4996);
}

.youtube {
  background-color: #c4032a;
}

.limeGreen {
  color: #1db489;
}

.brightRed {
  color: #dc414c;
}

.overview {
  display: grid;
  row-gap: 2rem;
  margin-bottom: 4rem;
}
.overview h2 {
  font-size: 2.4rem;
  color: white;
}
.overview__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 14rem;
  padding: 2.5rem;
  background-color: #252a41;
  border-radius: 0.5rem;
}
.overview__card:hover {
  cursor: pointer;
  background-color: #333b5b;
}
.overview__card--redSocial {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.overview__card--redSocial p {
  font-weight: 700;
  color: #8b97c6;
}
.overview__card--datas {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.overview__card--datas h2 {
  font-size: 3.5rem;
}
.overview__card--datas p {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 700;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  color: white;
}
.footer a {
  margin-left: 0.5rem;
  text-decoration: none;
  color: #8b97c6;
}